*{
    font-family: Manrope, sans-serif;
}

section{
    justify-content: center;
    align-items: center;
    display: flex;
    height: 100vh;
}
.parent{
    background: hsla(216, 19%, 38%, 0.59);
    width: 400px;
    height: 250px;
    padding: 2rem;
    text-align: center;
    border-radius: 0.5rem;
}
h2{
   color: hsl(193, 38%, 86%);
    font-size: 25px;
}
#line{
    
}

.span1{
    margin-right: 0.5rem;
    color:  hsl(150, 100%, 66%);
    font-size:20px ;
    
}
.span2{
   
    font-size: 20px;
    color:  hsl(150, 100%, 66%);
}
body{
    background: hsl(217, 19%, 24%);
}

.icon{
    border-radius: 50%;
    padding: 1rem;
    background: hsl(150, 100%, 66%);
    position: absolute;
    top:70%;
    left: 48.5%;
    border: none;
}
@media screen and (max-width:760px) {
    section{
        height: 100vh;
    }
    
    .icon{
        top:65%;
        left: 45%;
    }
    .parent{
        height: 250px;
    }
}